$(document).ready(function() {
	$('.datepicker-from').datepicker({
		changeMonth: true,
		changeYear: true,
		dateFormat: 'dd.mm.yy',
		yearRange: '2000:2040',
		monthNamesShort: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
		beforeShow: function(input, inst) {
			inst.dpDiv.css({
				transform: 'translateX(0)'
			});
		}
	}).inputmask({
		alias: 'datetime',
		inputFormat: 'dd.mm.yyyy',
		prefillYear: false,
		placeholder: '_',
		min: '01.01.2000',
		max: '12.31.2040',
		oncomplete: function() {
			var arr = $(this).val().split('/'),
				date = new Date(arr[2] + '-' + arr[1] + '-' + arr[0] + ' ' + '00:00:00');

			$(this).datepicker( "setDate",  date);
		}
	});

	$('.datepicker-to').datepicker({
		changeMonth: true,
		changeYear: true,
		dateFormat: 'dd.mm.yy',
		yearRange: '2000:2040',
		monthNamesShort: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
		beforeShow: function(input, inst) {
			inst.dpDiv.css({
				transform: 'translateX(-10px)'
			});
		}
	}).inputmask({
		alias: 'datetime',
		inputFormat: 'dd.mm.yyyy',
		prefillYear: false,
		placeholder: '_',
		min: '01.01.2000',
		max: '12.31.2040',
		oncomplete: function() {
			var arr = $(this).val().split('/'),
				date = new Date(arr[2] + '-' + arr[1] + '-' + arr[0] + ' ' + '00:00:00');

			$(this).datepicker( "setDate",  date);
		}
	});

	$(document).on('mouseup', function(e) {
		const langSwitcher = $('.lang-switcher');

		if (!langSwitcher.is(e.target) && langSwitcher.has(e.target).length === 0) {
			langSwitcher.removeClass('open');
		}
	});

	const langSwitcher = $('.lang-switcher');
	const langSwitcherLinks = $('.lang-switcher a');

	langSwitcher.on('click', function () {
		$(this).addClass('open');
	});

	langSwitcherLinks.on('click', function (e) {
		e.preventDefault();
		e.stopPropagation();
		langSwitcherLinks.removeAttr('style');

		if (!$(this).closest('.lang-switcher').hasClass('lang-switcher--reverse')) {
			$(this).css('order', '-1');
		} else {
			$(this).css('order', '1');
		}
		$(this).closest('.lang-switcher').removeClass('open');
	});

	$('.key-words select').select2({
		dropdownParent: $('.key-words')
	});

	if (window.matchMedia('(max-width: 639px)').matches) {
		$('.events-single .events-single__caption .caption__type').on('click', function () {
			const content = $(this).next();

			if (!$(this).hasClass('open')) {
				content.slideDown(300);
				$(this).addClass('open');
			} else {
				content.slideUp(300);
				$(this).removeClass('open');
			}
		});
	}

	/* Add clear button on opening dropdown */
	$('.key-words select').on('select2:open', function () {
		setTimeout(() => {
			$('.key-words .select2-results').append('<li class="select2-results__option select2-results__option--another select2-results__option--selectable">ANOTHER TAG<div class="key-words__close"><svg width="7" height="7" viewBox="0 0 7 7" fill="none" xmlns="http://www.w3.org/2000/svg">' +
				'<path d="M1 1L6 6"/>' +
				'<path d="M1 6L6 1"/>' +
				'</svg></div></li>');
		}, 0);
	});

	/* Remove clear button on closing dropdown */
	$('.key-words select').on('select2:closing', function () {
		$('.key-words .select2-results .select2-results__option--another ').remove();
	});

	/* Partners marquee */
	const marqueePartners = new Swiper('.js-marquee-partners', {
		slidesPerView: 'auto',
		spaceBetween: 0,
		loop: true,
		freeMode: true,
		autoplay: {
			delay: 1,
			disableOnInteraction: false,
			//pauseOnMouseEnter: true
		},
		speed: 6000
	});

	/* Content toggle button */
	$('.js-content-toggle').on('click', function () {
		const content = $(this).parent().next();

		if (content.is(':hidden')) {
			content.slideDown(300);
			$(this).text('CLOSE');
		} else {
			content.slideUp(300);
			$(this).text('OPEN');
		}
	});

	/* Attachments toggle button */
	$('.js-attachments-toggle').on('click', function () {
		const content = $(this).prev().find('ul');

		if (content.is(':hidden')) {
			content.slideDown(300);
			$(this).addClass('open');
			$(this).find('span').text('Show less');
		} else {
			content.slideUp(300);
			$(this).removeClass('open');
			$(this).find('span').text('Show more');
		}
	});

	/* Partners switcher */
	$('.filter-switcher__btn').click(function () {
		$(this).toggleClass('active');
	});
})
$(document).ready(function() {
	var burger = $('.header-m__burger-wrapper');
	var closeMenu = $('.header-menu__close');
	var header = $('.header');
	var headerOffset = header.offset().top;
	var headerHeight = header.outerHeight();
	var scroll = $(window).scrollTop();
	var isScroll = false;
	var toggledMenu = $('.header-menu nav li span');

	toggledMenu.click(function () {
		$(this).next().slideToggle(300);
	});

	burger.click(function () {
		if (!header.hasClass('open')) {
			header.addClass('open');
			$('body').addClass('body-scroll-lock');
		}
	});

	closeMenu.click(function () {
		header.removeClass('open');
		$('body').removeClass('body-scroll-lock');
	});

	$(window).on('scroll', function () {
		if (window.matchMedia('(min-width: 1280px)').matches) {
			scroll = $(window).scrollTop();

			if (scroll >= headerOffset + headerHeight) {
				isScroll = true;

				headerHeight = isScroll ? header.outerHeight() : null;
				header.addClass('header--fixed');

				if (!header.hasClass('is-fixed')) {
					header.css({'top': -headerHeight + 'px', 'transform': 'translateY(' + headerHeight + 'px)'}).addClass('is-fixed');
					//$('body').css('padding-top', headerHeight + 'px');
				}
			} else {
				isScroll = false;
				header.removeClass('header--fixed' + ' ' + 'is-fixed').removeAttr('style');
				//$('body').css('padding-top', 0);
			}
		}
	});

	let vh = window.innerHeight * 0.01;
	document.documentElement.style.setProperty('--vh', `${vh}px`);

	window.addEventListener('resize', () => {
		let vh = window.innerHeight * 0.01;
		document.documentElement.style.setProperty('--vh', `${vh}px`);
	});
})
$(document).ready(function() {
	const selects = $('.pdf__select-wrapper');
	const closeBtn = $('.pdf__close');

	selects.on('click', function () {
		$(this).addClass('open');
		$(this).siblings('.pdf__quantity').hide();
		$(this).siblings('.pdf__close').css('display', 'flex');

		$(this).closest('.card').children('.link-inset').hide();
	});

	closeBtn.on('click', function () {
		$(this).siblings('.pdf__select-wrapper').removeClass('open');
		$(this).siblings('.pdf__quantity').css('display', 'flex');
		$(this).hide();
		$(this).siblings('.pdf__select-wrapper').find('.pdf__select-scroll').animate({
			scrollTop: 0
		}, 0);

		$(this).closest('.card').children('.link-inset').show();
	});

	$(document).on('mouseup', function(e) {
		const target = $('.pdf__select-wrapper.open');

		if (target && !target.is(e.target) && target.has(e.target).length === 0) {
			target.removeClass('open');
			target.siblings('.pdf__quantity').css('display', 'flex');
			target.siblings('.pdf__close').hide();

			target.closest('.card').children('.link-inset').show();
		}
	});
})
$(document).ready(function() {
	let newsSlider = undefined;
	const newsSliderEl = $('.js-slider-news');
	const breakpoint = window.matchMedia( '(min-width: 1280px)' );

	const initSliders = () => {
		if (newsSliderEl.length > 0) {
			if (breakpoint.matches === true) {
				return enableSliders();
			} else if (breakpoint.matches === false) {
				destroySliders(newsSlider);
				return;
			}
		}
	}

	const enableSliders = () => {
		if (newsSlider == undefined || newsSlider.el == undefined) {
			newsSlider = new Swiper('.js-slider-news', {
				slidesPerView: 3.35,
				spaceBetween: 35,
				touchStartPreventDefault: false
			});
		}
	}

	const destroySliders = (sliderName) => {
		if (sliderName !== undefined) {
			if (sliderName.el !== undefined) {
				sliderName.el.querySelector('.swiper-wrapper').removeAttribute('style');
				sliderName.el.querySelector('.swiper-slide').removeAttribute('style');
			}

			sliderName.destroy();
			sliderName = undefined;
		}
	}

	initSliders();

	window.addEventListener('resize', function () {
		initSliders();
	});

	const eventsSlider = new Swiper('.js-slider-events', {
		slidesPerView: 'auto',
		spaceBetween: 35
	});

	const eventsSlider2 = new Swiper('.js-slider-events-2', {
		slidesPerView: 'auto',
		spaceBetween: 10,
		navigation: false,
		breakpoints: {
			640: {
				navigation: {
					nextEl: '.events-page__slider-next',
					prevEl: '.events-page__slider-prev',
				}
			},
			1280: {
				spaceBetween: 35
			}
		}
	});

	const photoSlider = new Swiper('.js-slider-photo', {
		slidesPerView: 1.27,
		spaceBetween: 10,
		breakpoints: {
			640: {
				slidesPerView: 1.5
			},
			1280: {
				slidesPerView: 2.2,
				spaceBetween: 20
			}
		}
	});

	Fancybox.bind('[data-fancybox="gallery"]', {
		Toolbar: {
			items: {
				close: {
					type: "button",
					label: "CLOSE",
					class: "fancybox__button--close",
					html: '<svg width="23" height="23" viewBox="0 0 23 23" fill="none" xmlns="http://www.w3.org/2000/svg">\n' +
						'<rect width="31.5254" height="1.00115" transform="matrix(0.7071 0.707113 -0.7071 0.707113 0.708008 0)" fill="#F7F8FC"/>\n' +
						'<rect width="31.5254" height="1.00115" transform="matrix(0.7071 0.707113 -0.7071 0.707113 0.708008 0)" fill="#F7F8FC"/>\n' +
						'<rect width="31.5254" height="1.00115" transform="matrix(-0.7071 0.707113 -0.7071 -0.707113 23 0.707764)" fill="#F7F8FC"/>\n' +
						'<rect width="31.5254" height="1.00115" transform="matrix(-0.7071 0.707113 -0.7071 -0.707113 23 0.707764)" fill="#F7F8FC"/>\n' +
						'</svg>\n',
					tabindex: 1,
					click: function (event) {
						event.stopPropagation();
						event.preventDefault();

						this.fancybox.close();
					},
				},
				prev: {
					type: "button",
					class: "fancybox__button--prev",
					label: "PREV",
					html: '<svg width="9" height="19" viewBox="0 0 9 19" fill="none" xmlns="http://www.w3.org/2000/svg">\n' +
						'<path d="M8 18L1 9.5L8 1" stroke="#F7F8FC"/>\n' +
						'</svg>\n',
					click: function (event) {
						event.preventDefault();

						this.fancybox.prev();
					},
				},
				next: {
					type: "button",
					class: "fancybox__button--next",
					label: "NEXT",
					html: '<svg width="9" height="19" viewBox="0 0 9 19" fill="none" xmlns="http://www.w3.org/2000/svg">\n' +
						'<path d="M1 1L8 9.5L1 18" stroke="#F7F8FC"/>\n' +
						'</svg>\n',
					click: function (event) {
						event.preventDefault();

						this.fancybox.next();
					},
				}
			}
		},
		Image: {
			Panzoom: {
				zoom: false,
				maxScale: 1,
				touch: false
			},
		}
	});

	const historySlider = new Swiper('.js-slider-history', {
		slidesPerView: 1.27,
		spaceBetween: 0,
		freeMode: true,
		breakpoints: {
			640: {
				slidesPerView: 1.5
			},
			1280: {
				slidesPerView: 3.74
			}
		}
	});
})